.form {
  position: relative;
  &__group:not(:last-child) {
    margin-bottom: 2rem;
  }

  &__area {
    width: 100%;
    position: relative;

    input[type='file'] {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      cursor: pointer;
      &:focus {
        outline: none;
      }
    }
    .file-dummy {
      width: 100%;
      padding: 3rem;
      border-radius: 4px;
      background-color: $color-content-light;
      border: 2px dashed $border-input-lines;
      text-align: center;
      transition: background 0.3s ease-in-out;

      .success {
        display: none;
      }
    }

    &:hover .file-dummy {
      background-color: $color-content-light;
    }

    input[type='file']:focus + .file-dummy {
      outline: 2px solid rgba(255, 255, 255, 0.5);
      outline: -webkit-focus-ring-color auto 5px;
    }

    input[type='file']:valid + .file-dummy {
      border-color: rgba(0, 255, 0, 0.4);
      background-color: rgba(0, 255, 0, 0.3);

      .success {
        display: inline-block;
      }
      .default {
        display: none;
      }
    }
  }

  &__input {
    background-color: $color-content-light;
    color: $color-text-dark;
    font-size: $font-size-default;
    padding: 1rem;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    border: 1px solid $border-input-lines;
    width: 100%;
    box-sizing: border-box;

    &:focus {
      outline: none;
      padding: 0.9rem;
      border: 2px solid $color-text-light;
      border-radius: 4px;
    }

    //   &:focus:invalid {

    //   }

    //   &::-webkit-input-placeholder {

    //   }
  }
  &__span {
    position: absolute;
    top: 0.7rem;
    left: 43%;
    color: $color-text-light;
    background-color: $color-content-light;
  }
  &__label {
    font-size: $font-size-default;
    margin-bottom: 0.7rem;
    display: block;
    transition: all 0.3s;
  }
  //   &__input:placeholder-shown + &__label {
  //     opacity: 0;
  //     visibility: hidden;
  //     transform: translateY(-4rem);
  //   }

  &__checkbox-group {
    margin-top: 1rem;
    display: flex;
    align-items: center;
  }
  &__checkbox {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 4px;
    border: 1px solid $border-input-lines;
    background-color: $color-content-light;
  }
  &__checkbox-label {
    margin-left: 0.7rem;
  }
  &__radio-group {
    margin: 1rem 0;
  }
  &__radio-input {
    display: none;
  }
  &__radio-label {
    font-size: $font-size-default;
    cursor: pointer;
    position: relative;
    padding-left: 4.5rem;
  }
  &__radio-button {
    height: 1.5rem;
    width: 1.5rem;
    border: 1px solid $border-input-lines;
    border-radius: 100%;
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;

    &::after {
      content: '';
      display: block;
      height: 1.5rem;
      width: 1.5rem;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: $color-white;
      border: 6px solid $color-link-light;
      opacity: 0;
      transition: opacity 0.2s;
    }
  }
  &__radio-input:checked ~ &__radio-label &__radio-button::after {
    opacity: 1;
  }

  &__footer {
    display: flex;
    padding: 2rem 1.5rem;
    margin-top: 2rem;
    background-color: $color-content-light;
  }
  hr {
    margin: 2.5rem 0;
  }

  &__map {
    width: 100%;
    height: 30rem;
    border-radius: 4px;
    border: 1px solid $border-input-lines;
  }
}
